<template>
  <div class="flex gap-10 m-8">
    <div
      v-if="!isVideoPlayed"
      class="flex justify-center items-center h-64 w-96 bg-gray-200 shadow-3xl rounded-md"
    >
      <BaseButton :handleButton="handleCamera">Connect</BaseButton>
    </div>
    <video v-else ref="video" class="h-64 rounded-md shadow-3xl" />
    <p v-if="error">{{ error }}</p>
    <div class="flex justify-center items-center h-64 w-96 bg-gray-200 shadow-3xl rounded-md">
      <p class="animate-pulse text-black">Waiting for player...</p>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'Frames',
  setup () {
    const video = ref()
    const error = ref('')
    const isVideoPlayed = ref(false)

    function handleCamera () {
      navigator.mediaDevices.getUserMedia({ video: true })
        .then(mediaStream => {
          video.value.srcObject = mediaStream
          video.value.play()
        })
        .catch(error => {
          console.error('getUserMedia() error:', error)
          error.value = error.message
        })
      isVideoPlayed.value = true
    }

    return {
      video,
      error,
      handleCamera,
      isVideoPlayed
    }
  }
})
</script>
