
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'LoseGame',
  props: {
    score: {
      type: Number,
      required: true
    }
  },
  emits: ['handle-restart'],
  setup (_, { emit }) {
    function handleRestart () {
      emit('handle-restart')
    }
    return {
      handleRestart
    }
  }
})
