<template>
  <div class="flex flex-col items-center gap-4">
    <h2 class="text-center text-primary font-semibold">You win the game</h2>
    <p class="text-center text-primary font-semibold">Your score: {{ score }}</p>
    <BaseButton :handleButton="sendData">
      Save your result
    </BaseButton>
    <BaseButton link="/">
      Home
    </BaseButton>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { useRouter } from 'vue-router'
import { sendScore } from '@/api/api'
import { localStorageService } from '@/services/localStorageService'

export default defineComponent({
  name: 'WinGame',
  props: {
    score: {
      type: Number,
      required: true
    }
  },
  setup () {
    const router = useRouter()

    function sendData () {
      const newUser = {
        name: `User ${+new Date()}`,
        score: localStorageService.getItem('score')
      }
      sendScore(newUser)
      router.replace('/')
    }

    return {
      sendData
    }
  }
})
</script>
